.ar_support_container {
    width: 21.4375rem;
    height: 3rem;

    position: absolute;
    top: 2rem;
    left: 50%;
    transform: translate(-50%, 0);
}

.ar_info_close_container {
    width: 3rem;
    height: 3rem;

    background-color: rgba(49, 49, 49, 0.8);
    border-radius: 1rem;

    position: absolute;
    top: 0;
    left: 0;
}

.ar_info_close {
    position: absolute;
    left: 1.4rem;
    top: 0.75rem;
    width: 1.5rem;
    height: 1.5rem;
}

.ar_info_close:before,
.ar_info_close:after {
    position: absolute;
    content: '';
    height: 1.5rem;
    width: 0.2rem;
    background-color: #FFFFFF;
}

.ar_info_close:before {
    transform: rotate(45deg);
}

.ar_info_close:after {
    transform: rotate(-45deg);
}

.ar_info_reset_container {
    width: 3rem;
    height: 3rem;

    background-color: rgba(49, 49, 49, 0.8);
    border-radius: 1rem;

    position: absolute;
    top: 0;
    right: 0;
}

.ar_info_reset {
    width: 1.8rem;
    height: 1.8rem;

    position: absolute;
    left: 0.6rem;
    top: 0.6rem;
}

.ar_info_container {
    width: 21.4375rem;

    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, 0);

    display: flex;
    flex-direction: column;
}

.ar_info {
    box-sizing: border-box;

    padding: 1rem;

    background-color: rgba(49, 49, 49, 0.8);
    border-radius: 0.625rem;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.ar_info_main {
    display: flex;
    justify-content: space-between;
}

.ar_info_main_title {
    font-family: "Roboto Light", sans-serif;
    font-weight: 700;
    font-size: 1.125rem;
    line-height: 1.75rem;
}

.ar_info_main_price {
    white-space: nowrap;
    font-family: "Roboto Light", sans-serif;
    font-weight: 700;
    font-size: 1.5rem;
    line-height: 1.5rem;
}

.ar_info_additional{
    margin-top: 1.5rem;
}

.ar_info_additional div {
    font-family: "Roboto Light", sans-serif;
    font-weight: 500;
    font-size: 1rem;
    line-height: 1.5rem;
    margin-top: 0.5rem;
}

.ar_info_additional span {
    font-family: "Roboto Light", sans-serif;
    font-weight: 700;
    font-size: 1rem;
    line-height: 1.5rem;
}

.ar_buttons {
    margin-top: 1rem;

    display: flex;
    flex-direction: column;
}

.ar_button_size,
.ar_button_buy {

    height: 3rem;

    font-family: Inter, sans-serif;
    font-weight: 500;
    font-size: 1rem;

    outline: none;
    border: none;
    border-radius: 0.625rem;
}

.ar_button_size {
    background-color: #FFFFFF;
    color: #000000;
}

.ar_button_buy {
    margin-top: 1rem;
    margin-bottom: 3rem;
    background-color: #DF661C;
    color: #FFFFFF;
}