.size_container {
    max-width: 21.375rem;
    height: 40.56rem;
    padding-left: 1rem;
    padding-right: 1rem;
    margin-top: 3rem;
    margin-left: auto;
    margin-right: auto;

    color: #FFFFFF;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.size_header {
    font-family: "Roboto Light", sans-serif;
    font-weight: 700;
    font-size: 1.5rem;
    line-height: 1.75rem;

}

.size_image_container {
    width: 21.4375rem;
    height: 20rem;
    display: flex;
    align-items: center;
}

.size_image {
    padding: 0;
    margin: 0;
    width: 100%;
}

.size_input_list {
    width: 11rem;

    font-family: Inter, sans-serif;
    font-weight: 500;
    font-size: 1rem;
    line-height: 1.5rem;
}

.size_accept_button {
    font-family: Inter, sans-serif;
    font-weight: 500;
    font-size: 1rem;

    width: 100%;
    height: 3rem;

    outline: none;
    border: none;
    border-radius: 0.625rem;

    background-color: #DF661C;
    color: #FFFFFF;

    margin-top: 4rem;
}