.catalog_container {
    max-width: 21.375rem;
    padding-left: 1rem;
    padding-right: 1rem;
    margin-left: auto;
    margin-right: auto;

    color: #FFFFFF;
}

.catalog_header {
    box-sizing: border-box;
    margin-top: 3rem;

    font-size: 1.5rem;
    font-weight: 700;
}

.catalog_list {
    width: 100%;
    margin-top: 1.5rem;
    padding: 0;

    list-style: none;
}

.catalog_item {
    box-sizing: border-box;
    width: 100%;
    margin-top: 1rem;

    border-radius: 0.625rem;
    text-decoration: none;

    position: relative;
    display: flex;
}

.item_link_category {
    width: 100%;

    text-decoration: none;
    align-items: center;

    display: flex;
}

.item_link_category_image {
    width: 100%;
    height: 5.375rem;

    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
}

.item_link_category_title {
    font-size: 1.125rem;
    font-weight: 500;
    margin-left: 1.25rem;
    position: absolute;
    color: #FFFFFF;
}

.item_link_category_size {
    position: absolute;
    right: 0
}

.item_link_category_size img {
    border-top-right-radius: 0.625rem;
}

