.size_input_item {
    margin-top: 1rem;

    display: flex;
    justify-content: space-between;
    align-items: center;
}

.size_input {
    font-family: Inter, sans-serif;
    font-weight: 500;
    font-size: 1rem;

    width: 3.75rem;
    height: 2.5rem;

    outline: none;
    border: none;
    border-radius: 0.625rem;
    text-align: center;

    background-color: #555456;
    color: #FFFFFF;
}

.size_input::-webkit-input-placeholder {
    color: #999;
}
.size_input:focus::-webkit-input-placeholder {
    color: transparent;
}
/* Firefox < 19 */
.size_input:-moz-placeholder {
    color: #999;
}
.size_input:focus:-moz-placeholder {
    color: transparent;
}
/* Firefox > 19 */
.size_input::-moz-placeholder {
    color: #999;
}
.size_input:focus::-moz-placeholder {
    color: transparent;
}

/* Internet Explorer 10 */
.size_input:-ms-input-placeholder {
    color: #999;
}
.size_input:focus:-ms-input-placeholder {
    color: transparent;
}


.size_input_item input::-webkit-input-placeholder {
    color: #FFFFFF;
    opacity: 40%;
}

.size_input_item input::-webkit-outer-spin-button,
.size_input_item input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

.input[type=number] {
    -moz-appearance: textfield;
}

.size_input_item label {
    width: 4rem;
}