.category_container {
    max-width: 21.375rem;
    padding-left: 1rem;
    padding-right: 1rem;
    margin-left: auto;
    margin-right: auto;

    color: #FFFFFF;
}

.category_header {
    margin-top: 3rem;

    font-size: 1.5rem;
    font-weight: 700;
    line-height: 1.5rem;
}

.category_list {
    width: 100%;
    margin-top: 1.5rem;
    padding: 0;

    font-family: "Roboto Light", sans-serif;
    font-weight: 700;
    font-size: 1.125rem;

    list-style: none;
}

.category_item {
    box-sizing: border-box;

    width: 100%;
    height: 19.875rem;
    margin-top: 1rem;
    padding-top: 0.75rem;
    padding-left: 0.75rem;
    padding-right: 0.75rem;

    border-radius: 0.625rem;
    background-color: #4B4B4B;

    position: relative;
    display: flex;
    flex-direction: column;
}

.category_item_image_container {
    box-sizing: border-box;

    height: 10.45rem;
    padding: 2rem;
    text-align: center;

    background-color: #FFFFFF;
    position: relative;
}

.category_item_image {
    height: 100%;
}

.category_item_ar_image {
    position: absolute;
    top: -1rem;
    right: 0;
}

.category_item_title {
    margin-top: 1.125rem;
}


.category_additional_info_container {
    margin-top: 0.5rem;

    font-weight: 500;
    font-size: 0.75rem;
    line-height: 1.5rem;
}

.category_item_size > span,
.category_item_color > span {
    font-weight: 700;
}

.category_item_price {
    margin-top: 0.625rem;

    text-align: right;
    font-size: 1.5rem;
    font-weight: 700;
    line-height: 1.5rem;
}

.category_item_apple_link{
    text-decoration: none;
    color: #FFFFFF;
}

.category_item_apple_link:active {
    outline: none;
}

